.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  margin-bottom: 2rem;
}

.wrapper__title {
  width: 100%;
  padding: 1rem;
  background: var(--gradient-bg);
  color: rgb(248, 245, 252);
  font-size: 2rem;
  text-align: center;
}
.wrapper__title.reverse {
  background: var(--gradient-bg-reverse);
}

.image {
  object-fit: contain;
  width: 90%;
  max-height: 370px;
}

.heading {
  font-size: 1rem;
  width: 90%;
  color: var(--text-color);
  text-transform: uppercase;
  text-align: center;
}

.clipboard {
  width: 50%;
  background-color: var(--purple-light);
  color: var(--text-color);
  font-size: 1rem;
  cursor: pointer;
  padding: 0.4rem 0.2rem;
  text-align: center;
  border-radius: 5px;
  position: relative;
}
.clipboard:hover {
  background-color: var(--purple-dark);
  color: #fff;
}

.clipboard-copied {
  width: 50%;
  color: var(--text-color);
  background-color: #fff;
  font-size: 0.8rem;
  text-align: center;
  padding: 0.2rem 0.1rem;
  border-radius: 5px;
  border: 0.3px solid var(--dark-bg);
  position: absolute;
  right: -45px;
  top: -19px;
  opacity: 0;
}

.button {
  width: 100%;
  background-color: var(--dark-bg);
  color: var(--green);
  padding: 1rem 2rem;
  font-family: var(--font-family);
  font-size: 1.2rem;
  font-weight: 700;
  border: none;
  outline: none;
}

.button:hover {
  opacity: 0.9;
}

.sad-icon {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.sad-icon-img {
  width: 200px;
  height: 200px;
}

.gif-container {
  flex: 2 1;
  display: flex;
  justify-content: space-between;
  grid-gap: 1rem;
  gap: 1rem;
}

.trending-item,
.search-item {
  width: 50%;
  background-color: rgb(252, 252, 252);
  box-shadow: 0px 1px 11px 6px rgba(255, 255, 255, 0.2);
}

.app {
  display: flex;
  flex-direction: column;
  max-width: 1200px;
  height: 100vh;
  padding: 1rem 1rem;
  margin: 0 auto;

  font-family: var(--font-family);
  color: var(--text-color);
}

:root {
  --font-family: "M PLUS Rounded 1c", sans-serif;

  --dark-bg: rgb(22, 10, 24);
  --text-color: rgb(12, 12, 12);
  --green: #59c173;
  --purple-dark: #5d26c1;
  --purple-light: #a17fe0;
  --gradient-bg: -webkit-linear-gradient(to right, #5d26c1, #a17fe0, #59c173);
  --gradient-bg: linear-gradient(to right, #5d26c1, #a17fe0, #59c173);

  --gradient-bg-reverse: -webkit-linear-gradient(
    to left,
    #5d26c1,
    #a17fe0,
    #59c173
  ); /* Chrome 10-25, Safari 5.1-6 */
  --gradient-bg-reverse: linear-gradient(
    to left,
    #5d26c1,
    #a17fe0,
    #59c173
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

/* Resets */

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

